.header {
    background-color: $color-mine-shaft;
    color: $color-white;
    //line-height: 52px;
}

.header__top {
    height: 52px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 0.1rem solid $color-mercury;
    position: relative;
    background-color: $color-mine-shaft;
}

.header__name{
    font-size: 2rem;
    padding: 1.5rem;
    display: inline-block;
}

.rbcf-menu {
    padding: 1.5rem;
    font-size: 2.5rem;
    cursor: pointer;
}

.header__menu-item {
    &:first-child {
        &:after {
            font-family: 'roberto-blake-custom-font';
            content: "\e902";
            font-size: 2rem;
            padding: 0 1.5rem;
        }
    }
}

.header__menu-item{
    background-color: $color-mine-shaft;
}

.header__menu-link,
.header__menu-sub-link{
    color: $color-white;
    font-size:1.5rem;
    text-decoration: none;
    padding: 0.75rem 5rem;
    display: block;
    cursor: pointer;
}
.header__menu-sub-link {
    padding: 0.75rem 2.5rem;
}

.header__menu-list{
    //display:none;
    transition: 0.3s;
    position:fixed;
    top: 5.2rem;
    left: 0;
    right: 0;
    border: 0.1rem solid $color-mercury;
}
#header__sub-menu,
#toggle-top-menu {
    display: none;
}
#toggle-top-menu:checked + .header__menu-list {
    //display:inherit;
    top: -100%;
}

.header__sub-menu{
    display: none;
}

#header__sub-menu:checked +.header__menu-link + .header__sub-menu {
    display: inherit;
}
#header__sub-menu:checked +.header__menu-link .rbcf-circle-down:before {
    transform: rotate(180deg);
    display: inline-block;
}

.rbcf-circle-down{
    padding: 0.1rem;
}
